@import url(https://fonts.googleapis.com/css2?family=Orbitron&family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
/* TODO Add site wide styles */
html,
body {
    padding: 0;
    margin: 0;
    background: rgb(60,59,59);
    background: linear-gradient(0deg, rgba(60,59,59,1) 0%, rgba(144,144,144,1) 22%, rgba(203,203,203,1) 100%);
    font-family: 'Orbitron', sans-serif;


}

* {
    box-sizing: border-box;
}

.charSheet{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-around;
    width: 50vw;
    align-items: flex-start;

}
.charSheetHolder{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
    border-radius: 10px;
    padding:20px;
    background-color: rgba(255, 255, 255, 0.5);

}

.stats{
    border-radius: 10px;
    display: flex;
    align-content: stretch;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    background-color: rgba(255, 255, 255, 0.5);
    box-shadow: 5px 5px 5px rgba(0, 0, 0, .5) ;
}

.stat{
    padding: 10px;
}

button{
    box-shadow: 5px 5px 5px rgba(0, 0, 0, .5);
    /* background-color: rgba(56, 240, 253, 0.5); */
    background-color: rgba(255, 255, 255, 0.5);
    color: black;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    margin: 4px 2px;
    margin-bottom: 20px;
    cursor: pointer;
    border-radius: 16px;
    border: none;
}

button:hover{
    transition: background-color .25s ;

    background-color:rgba(25, 252, 138, 0.5);
}

.suggestion{
    text-align: center;
    border-radius: 10px;
    padding: 10px;
    width: 25vw;
    background-color: rgba(255, 255, 255, 0.5);
    box-shadow: 5px 5px 5px rgba(0, 0, 0, .5)
}

@media screen and (max-width: 1151px){
    .charSheet{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-content: center;
        justify-content: space-around;
        grid-gap: 20px 20px;
        gap: 20px 20px;
        align-items: flex-start;

    }

    .suggestion{
        text-align: center;
        border-radius: 10px;
        padding: 10px;
        width: 90vw;
        font-size: .8em;
        background-color: rgba(255, 255, 255, 0.5);
        box-shadow: 5px 5px 5px rgba(0, 0, 0, .5)
    }

}

.about{
    border-radius: 10px;
    padding: 5px;
    width: 75vw;
    background-color: rgba(255, 255, 255, 0.5);
    margin: 10px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, .5)
}

.aboutText{
    margin: 10px;
    text-align: center;
}

@media screen and (max-width: 1151px){
    .about{
        border-radius: 10px;
        padding: 5px;
        width: 90vw;
        background-color: rgba(255, 255, 255, 0.5);
        margin: 10px;
        box-shadow: 5px 5px 5px rgba(0, 0, 0, .5)
    }
}

.skillHolder{
    width: 25em;
    height: 25em;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    border-radius: 10px;
    align-content: space-around;
    background-color: rgba(255, 255, 255, 0.5);
    box-shadow: 5px 5px 5px rgba(0, 0, 0, .5);
    padding: 10px;
}
svg{
    width: 25%;
    height: 25%;
}

svg:hover{
    fill:rgba(25, 252, 138, 0.5)
}

.siteHolder, .siteDescription{
    border-radius: 10px;
    padding: 5px;
    /* width: 40vw; */
    background-color: rgba(255, 255, 255, 0.5);
    box-shadow: 5px 5px 5px rgba(0, 0, 0, .5)
}

.logo{
    width: 33%;
}
.focused{
    width: 33%;
    -webkit-filter: drop-shadow(0px 0px 10px rgba(25, 252, 138));
            filter: drop-shadow(0px 0px 10px rgba(25, 252, 138));
}

.siteContent{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    max-width: 45em;
}

.siteDescription{
    padding: 10px;
    text-align: center;
    margin-top: 10px;
}

h1{
    font-family: 'Press Start 2P', cursive;

}

a{
    text-decoration: none;
    font-family: 'Press Start 2P', cursive;
    color:black;
}
a:hover{
    color: rgba(25, 252, 138)
}
.header{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    grid-gap: 20px 10px;
    gap: 20px 10px;
